import React from 'react';

import Card from '../components/Card';

import upandunder from '../assets/upandunder.png';
import cashcounter from '../assets/cashcounter.png';
import nba from '../assets/nba.png';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';


class Carousel extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            items: [
                {
                    id: 0,
                    title: 'Up and Under Podcast',
                    subTitle: 'A weekly basketball news and opnions podcast',
                    imgSrc: upandunder,
                    link: 'https://upandunderpodcast.com',
                    selected: false
                },
                {
                    id: 1,
                    title: 'Cash Counter',
                    subTitle: 'A handy tool to count cash in a flash',
                    imgSrc: cashcounter,
                    link: 'https://hanisalehi.com/cashcounter',
                    selected: false
                },
                {
                    id: 2,
                    title: 'NBA Player Search',
                    subTitle: 'A python algorithm that searches for NBA player stats.',
                    imgSrc: nba,
                    link: 'https://github.com/hsalehi100/nba-player-search',
                    selected: false
                }
            ]
        }
    }

    handleCardClick = (id, card) => {
        let items = [...this.state.items];

        items[id].selected = items[id].selected ? false : true;

        items.forEach(item => {
            if(item.id !== id) {
                item.selected = false;
            }
        });

        this.setState({
            items
        });
    }

    makeItems = (items) => {
        return items.map(item => {
            return <Card item={item} click={(e => this.handleCardClick(item.id, e))} key={items.id}/>
        })
    }


    render() {
        return(
            <Container fluid={true}>
                <Row className="justify-content-around pr-5 pl-5">
                    {this.makeItems(this.state.items)}
                </Row>
            </Container>
        );
    }

}

export default Carousel;