import React from 'react';
import {BrowserRouter as Router, Route, Link} from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import './App.css';

import Footer from './components/Footer';
import HomePage from './pages/HomePage';
import AboutPage from './pages/AboutPage';
import ContactPage from './pages/ContactPage';

import logo from './assets/logo.png';


class App extends React.Component{

  constructor(props){
    super(props);
    this.state = {
      title: 'Hani Salehi',
      headerLinks: [
        { title: 'Home', path: '/' },
        { title: 'About', path: '/about' },
        { title: 'Contact', path: '/contact' },
      ],

      home: {
        title: 'Welcome to My Web Playground',
        subTitle: 'Take a look around',
        text: 'Checkout my projects below'
      },

      about: {
        title: 'About Me',
        subTitle: 'Here is snippet of who I am'
      },

      contact: {
        title: 'Let\'s Chat',
        
      }
    }
  }

  render(){
    return (
      <Router>
        <Container className="p-0" fluid={true}>
          
          <Navbar className="border-bottom bg-light"  expand="lg">

            <Navbar.Brand>
              <img
                src={logo}
                width="30"
                height="30"
                className="d-inline-block align-top mr-2"
                alt="logo"
              />
              Hani Salehi
            </Navbar.Brand>

            <Navbar.Toggle className="border-0" aria-controls="navabar-toggle"/>
            <Navbar.Collapse id="navbar-toggle">
              <Nav className="ml-auto">
                <Link className="nav-link" to="/">Home</Link>
                <Link className="nav-link" to="/about">About</Link>
                <Link className="nav-link" to="/contact">Contact</Link>

              </Nav>
            </Navbar.Collapse>
          </Navbar>


          <Route path="/" exact render={ () =>  <HomePage title={this.state.home.title} subTitle={this.state.home.subTitle} text={this.state.home.text}/>}/>
          <Route path="/about" render={ () =>  <AboutPage title={this.state.about.title} subTitle={this.state.about.subTitle}/>}/>
          <Route path="/contact" render={ () =>  <ContactPage title={this.state.contact.title} />}/>

          <Footer />


        </Container>
      </Router>
    );

  }  
}

export default App;
