import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';


function Footer(){

    return(
        <footer className="mt-5">
            <Container fluid={true} className="bg-light">
                <Row className="border-top justify-content-between p-2 ">
                    <Col className="p-4 ml-5" md={3} sm={12}>
                        <p className="secondary-text"><strong>Welcome:</strong></p>
                        <p>Hello friend, my name is Hani. I'm an aspiring software developer, podcaster, and massive sports fan!</p>
                        
                    </Col>

                    <Col className="p-4 ml-5" md={3} sm={12}>
                        <p className="secondary-text"><strong>Follow Me:</strong></p>
                        <p className="m-0">Twitter: <a href="http://twitter.com/hsalehi1000" target="_blank" rel="noopener noreferrer">@hsalehi100</a></p>
                        <p className="m-0">Linkedin: <a href="https://www.linkedin.com/in/hsalehi313/" target="_blank" rel="noopener noreferrer">@hsalehi313</a></p>
                        <p className="m-0">Instagram: <a href="http://instagram.com/hsalehi100" target="_blank" rel="noopener noreferrer">@hsalehi100</a></p>
                    </Col>

                    <Col className="p-4 d-flex justify-content-end mr-5" md={3}>
                        This website was created by Hani Salehi.
                    </Col>
                </Row>
            </Container>
        </footer>


    );

}


export default Footer;