import React from 'react';

import Hero from '../components/Hero';
import Content from '../components/Content';

function AboutPage(props){

    return(
        <div>
            <Hero title={props.title} subTitle={props.subTitle}/>

            <Content>
                <p>Hello friend, my name is Hani. I am a third year Computer Science student who loves to build and create all sorts of fun projects.</p>

                <p>I generally consider myself as a jack of all trades as I can create any form of digital project one may desire. Whether it is graphic, media, or even web based you name it, I can make it! Currently I am experienced in HTML, CSS, JavaScript, Python, and Java. I have also spent my spare time learning the Adobe creative suite specifically Adobe Photoshop, Illustrator, Premiere, and XD. You can check out some of my dev projects by visiting my GitHub page by clicking <a href="http://github.com/hsalehi100" target="_blank" rel="noopener noreferrer">here</a>, as well as some of my graphic projects by clicking <a href="http://dribbble.com/hsalehi313" target="_blank" rel="noopener noreferrer">here</a>.</p>
            
                <p>I am also an avid sports fan specifically of basketball, football, and hockey but I do dabble in other sports as well. I love talking, reading and listening about sports and discussing the nuances of plays and news with anyone who can bare to listen to me go on and on. I even co-host and create my own basketball podcast called the Up and Under Podcast which you can find by clicking <a href="http://upandunderpodcast.com" target="_blank" rel="noopener noreferrer">here</a>.</p>

                <p>My dream is to one day blend my passions of creating with my love of sports and to potentially create a business for sports fans that allows us to merge our love for sports with our creativity to share with the world.</p>

                <p>Although I have learned a lot over my years (which even surprises myself), I always find myself constantly curious and learning new things. In fact I am currently learning and gaining experience in React, Node JS, and even further depening my understanding of Python. I would also love the opportunity to dive into what I believe will be the future of technology in machine learning and artificial intelligence.</p>

                <p>I would love to live in a world one day where the technology we see in movies like Tony Stark's glasses become a reality and I would love the opportunity to play a part in making what many thought were fictional and impossible dreams into reality.</p>

                <p>Overall I see the world as an empty canvas, and I am excited at all the possibilities there are to come in the future.</p>
            
            
            </Content>
        </div>
    );



}


export default AboutPage;